<template>
    <el-select v-model="citySite" multiple placeholder="请选择服务范围" @change="citySiteHandel">
      <template v-if="citySiteList.length" v-for="citySite in citySiteList">
        <el-option :label="citySite.cityName" :value="citySite.cityName" :key="citySite.cityName">
        </el-option>
      </template>
    </el-select>
</template>

<script>

  export default {
    data() {
      return {
        citySite: [] 
      }
    },
    props: {
      value: {
        type: [String, Array],
        default: ''
      },
      citySiteList: {
        type: Array,
        default: function(){
          return []
        }
      }
    },
    created (){
      if(this.value) {
        this.citySite = this.value.split(',')
      }
      
    },
    methods: {
      citySiteHandel (value){
        const citySiteStr = this.updateSysArea(value)
        this.$emit('input', citySiteStr)
      },
      updateSysArea (value){
        if(value.length) {
          return value.join(',')
        }
      }
    }
  }
</script>

<style lang="scss">

</style>


